<template>
  <div class="row store-logo-section">
    <div class="store-logo-group"
      :class="[layoutClass]"
      v-for="(store, index) in stores"
      :key="index">
      <store-icons
        :link="store.link"
        :ariaLabel="store.ariaLabel"
        :image="getStoreIconsImageUrl(store.image)"
        :altText="store.altText">
      </store-icons>
    </div>
  </div>  
</template>

<script>
import StoreIcons from '@/components/StoreIcons'  
export default {
  name: 'StoreIconsData',
  components: {
    StoreIcons
  },
  props: {
    layout: String,
  },
  data () {
    return {
      stores: [
        {
          link: "https://open.spotify.com/album/0uhbZw88pjQHESVgJPsMie",
          ariaLabel: "Play on Spotify (opens in new tab)",
          image: "spotify.png",
          altText: "Spotify Logo"
        },
        {
          link: "https://play.google.com/store/music/album/Midnight_Laboratory_Potent_Flask?id=Bdgijhxvsorc7byr7hlyxcmtpli",
          ariaLabel: "Purchase on Google Play (opens in new tab)",
          image: "google-play.png",
          altText: "Google Play Logo"
        },
        {
          link: "https://itunes.apple.com/us/album/potent-flask/id1265928375?uo=4&amp;app=music&amp;at=1001lry3&amp;ct=dashboard",
          ariaLabel: "Purchase on Apple Music (opens in new tab)",
          image: "apple-music.svg",
          altText: "Apple Music Logo"
        },
        {
          link: "https://www.amazon.com/Potent-Flask-Midnight-Laboratory/dp/B074F9GMZC?SubscriptionId=AKIAJDS6FAHQ4U6KQBSQ&amp;tag=distrokid06-20&amp;linkCode=xm2&amp;camp=2025&amp;creative=165953&amp;creativeASIN=B074F9GMZC/?tag=distrokid06-20",
          ariaLabel: "Purchase on Amazon (opens in new tab)",
          image: "amazon.png",
          altText: "Amazon Logo"
        },
        {
          link: "https://itunes.apple.com/us/album/potent-flask/id1265928375?uo=4&amp;app=itunes&amp;at=1001lry3&amp;ct=dashboard",
          ariaLabel: "Purchase on I Tunes (opens in new tab)",
          image: "iTunes.svg",
          altText: "iTunes Logo"
        },
        {
          link: "https://www.youtube.com/watch?v=BhUujJnAVQw&list=OLAK5uy_luY81C2AxRV62k94ZmdtKEKFBe2nZvX8Q",
          ariaLabel: "Purchase on You Tube (opens in new tab)",
          image: "youtube.png",
          altText: "YouTube Logo"
        },
        {
          link: "https://us.napster.com/artist/midnight-laboratory/album/potent-flask",
          ariaLabel: "Play on Napster (opens in new tab)",
